@import '~src/css/quasar.variables.scss', 'quasar/src/css/variables.sass';














































































.result-item {
    border-bottom: 1px solid $grey-8;
}
.result-item:last-child {
    border-bottom: 0;
}
.result-value {
    width: 240px;
    overflow: hidden;
    white-space: nowrap;
    text-overflow: ellipsis;
}
.menu-arrow {
    width: 12px;
    margin-left: 26px;
    height: 8px;
    clip-path: polygon(50% 0%, 0% 100%, 100% 100%);
}
