@import '~src/css/quasar.variables.scss', 'quasar/src/css/variables.sass';



















































































































































.fade-enter-active,
.fade-leave-active {
    transition: opacity 0.5s;
}
.fade-enter,
.fade-leave-to {
    opacity: 0;
}
.slide-enter-active,
.slide-leave-active {
    transition: all 0.5s;
}
.slide-leave-active {
    position: absolute;
}
.slide-move {
    transition: transform 0.5s;
}
.slide-enter {
    transform: translateY(30px);
    opacity: 0;
}
.slide-leave-to {
    transform: translateY(30px);
    opacity: 0;
}
.no-select {
    user-select: none;
}
.sm-layout .q-drawer {
    background: #1a1d1a !important;
}
.q-drawer {
    position: fixed;
}
.scrolled {
    background: #1a1d1a !important;
}
.adjust-content-left {
    justify-content: left;
}
.capitalize,
.alias {
    text-transform: capitalize;
}
.q-drawer .q-router-link--active {
    border-right: 3px solid $primary;
}

@media (max-width: 480px) {
    .quick-search {
        display: none;
    }
}
